@import url(https://fonts.googleapis.com/css?family=Asap|Comfortaa);
@import url(https://fonts.googleapis.com/css?family=Asap|Comfortaa);
@import url(https://fonts.googleapis.com/css?family=Asap|Comfortaa);
@import url(https://fonts.googleapis.com/css?family=Asap|Comfortaa);
body {
  margin: 0;
  font-family: 'Comfortaa', BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  width: 100;
  height: 100vh;
  background-color: #e8e8e8;
  font-family: Comfortaa;
}


​
.App-logo {
  height: 40vmin;
  pointer-events: none;
}
​
@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}
​
.App-header {
  background-color: #283428;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
​
.App-link {
  color: #61dafb;
}
​
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
​
.news-container {
  text-align: center;
  margin: auto;
  width: 90%;
}

.bottom_buffer{
  padding: 25px;
}

.img{
  max-width: 25%;
  height: auto;
  float: left;
  padding-bottom: 10px;
}

.text-secondary{
  font-family: 'Asap';
  padding: 25px;
}

.sidebar {
  z-index: 1000;
  position: absolute;
  left: 200px;
  width: 200px;
  height: 100%;
  margin-left: -200px;
  overflow-y: auto;
  transition: all 0.5s ease;
}

.sidebar-nav {
  list-style: none;
  margin: 0;
  padding: 0;
}

.content {
  margin-left: 200px;
}

.table-wrapper {
  overflow: hidden;
  overflow-y: scroll;
  height: 80vh;
}

.App {
  text-align: center;
}

.Video-Frame {
  min-height: 425px;
  min-width: 375px;
  width: 80%;
  height: 80%;
}

